<template>
  <div class="fixed w-full h-full left-0 top-0 bg-[lightblue] flex justify-center items-center px-[2%] box-border">
    <div class="max-w-3xl bg-white w-full h-80 text-center rounded-lg shadow-lg">
      <h3 class="text-[120px] font-bold mb-18 text-orange">404 Error</h3>
      <p class="text-lg font-normal mt-0 mb-5">페이지를 찾을 수 없습니다.</p>
      <p class="text-lg font-normal mt-0 mb-5">아래 버튼을 눌러 메인으로 이동하시거나 <span>{{ count }}</span>초 뒤 메인 페이지로 이동합니다.</p>
      <router-link to="/" class="py-1 px-10 border rounded transition-all hover:text-white hover:shadow-[inset_-72px_0_0_0_orange,inset_72px_0_0_0_orange] duration-700">메인으로</router-link>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
    count: 5
    }
  },
  mounted(){
    this.CountDown();
  },
  methods:{
    CountDown(){
      if(this.count > 0){
        setTimeout(() => {
          this.count--;
          this.CountDown()
        }, 1000);  
      }else{
        this.$router.replace('/');
      }
    }
  }
}

</script>

